import s from './ProfileContacts.module.css'

const ProfileContacts = ({ Profile, setEditMod, id }) => {
    let notSpecified = "---"

    if (Profile === null) {
        return <div>загрузка</div>
    }
    if (Profile != null) {
        return <div >
            <div className={s.ProfileContacts__button}>
                <button className={Profile.userId != id && s.disabledBut} disabled={Profile.userId === id ? false : true}  onClick={() => setEditMod(true)}>Редактировать</button>
            </div>


            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>О себе</span>
                {Profile.aboutMe || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>Ищу работу?</span>
                {Profile.lookingForAJob ? 'Да' : 'Нет'}
            </div>
            {Profile.lookingForAJob &&
                <div className={s.ProfileContacts}>
                    <span className={s.ProfileContacts__span}>Мои скилы</span>
                    {Profile.lookingForAJobDescription}
                </div>
            }

            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>facebook </span>
                {Profile.contacts.facebook || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>instagram </span>
                {Profile.contacts.instagram || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>github </span>
                {Profile.contacts.github || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>vk </span>
                {Profile.contacts.vk || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>twitter </span>
                {Profile.contacts.twitter || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>website </span>
                {Profile.contacts.website || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>youtube </span>
                {Profile.contacts.youtube || notSpecified}
            </div>
            <div className={s.ProfileContacts}>
                <span className={s.ProfileContacts__span}>mainLink </span>
                {Profile.contacts.mainLink || notSpecified}
            </div>
        </div>

    }

}


export default ProfileContacts;